import { useCallback } from "react"
import { useAtom } from "jotai"
import { atomWithStorage } from "jotai/utils"
import { useUser } from "@clerk/clerk-react"
import { isAdminUser } from "@rp/common/utils"

const webSocketEndpointAtom = atomWithStorage<undefined | string>("wsOverride", undefined)

const useWebSocketEndpointAtom = () => {
  const { user } = useUser()
  const [webSocketEndpoint, setWebsocketEndpoint] = useAtom(webSocketEndpointAtom)

  const resetWebSocketEndpoint = useCallback(() => {
    setWebsocketEndpoint(undefined)
  }, [setWebsocketEndpoint])

  return {
    webSocketEndpoint: isAdminUser(user?.primaryEmailAddress?.emailAddress ?? "")
      ? webSocketEndpoint
      : undefined,
    setWebsocketEndpoint,
    resetWebSocketEndpoint,
  }
}

export { useWebSocketEndpointAtom }
